'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { useStore } from '@/store/useStore';
import { Game } from '@/types';
import { cn } from '@/utils';

interface GameCardProps {
  game: Game;
  className?: string;
}

const GameCard: React.FC<GameCardProps> = ({ game, className }) => {
  const { language, isAuthenticated, setLoginModalOpen } = useStore();

  const handlePlayGame = () => {
    if (!isAuthenticated) {
      setLoginModalOpen(true);
      return;
    }
    // Navigate to game or open game modal
    console.log('Playing game:', game.id);
  };

  return (
    <motion.div
      whileHover={{ scale: 1.05, y: -5 }}
      whileTap={{ scale: 0.98 }}
      className={cn(
        'relative bg-white rounded-xl shadow-lg overflow-hidden cursor-pointer group',
        className
      )}
      onClick={handlePlayGame}
    >
      {/* Game Image */}
      <div className="relative aspect-[4/3] overflow-hidden">
        <div className="w-full h-full bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center">
          {/* Placeholder for game image */}
          <div className="text-white text-4xl font-bold">
            {game.name.charAt(0)}
          </div>
        </div>
        
        {/* Overlay */}
        <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-40 transition-all duration-300 flex items-center justify-center">
          <motion.button
            initial={{ opacity: 0, scale: 0.8 }}
            whileHover={{ opacity: 1, scale: 1 }}
            className="bg-orange-500 hover:bg-orange-600 text-white px-6 py-2 rounded-full font-medium opacity-0 group-hover:opacity-100 transition-all duration-300"
          >
            {language.code === 'vi' ? 'Chơi Ngay' : 'Play Now'}
          </motion.button>
        </div>

        {/* Badges */}
        <div className="absolute top-2 left-2 flex flex-col gap-1">
          {game.isNew && (
            <span className="bg-green-500 text-white text-xs px-2 py-1 rounded-full font-medium">
              {language.code === 'vi' ? 'MỚI' : 'NEW'}
            </span>
          )}
          {game.isHot && (
            <span className="bg-red-500 text-white text-xs px-2 py-1 rounded-full font-medium">
              {language.code === 'vi' ? 'HOT' : 'HOT'}
            </span>
          )}
        </div>

        {/* Provider */}
        <div className="absolute top-2 right-2">
          <span className="bg-black bg-opacity-50 text-white text-xs px-2 py-1 rounded">
            {game.provider}
          </span>
        </div>
      </div>

      {/* Game Info */}
      <div className="p-4">
        <h3 className="font-semibold text-gray-900 mb-1 truncate">
          {language.code === 'vi' ? game.name : game.nameEn}
        </h3>
        <p className="text-sm text-gray-600 mb-3 line-clamp-2">
          {language.code === 'vi' ? game.description : game.descriptionEn}
        </p>
        
        {/* Category */}
        <div className="flex items-center justify-between">
          <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded">
            {game.category.toUpperCase()}
          </span>
          
          {/* Rating or other info */}
          <div className="flex items-center text-yellow-500">
            <span className="text-sm">⭐</span>
            <span className="text-xs text-gray-600 ml-1">4.8</span>
          </div>
        </div>
      </div>

      {/* Hover Effect Border */}
      <div className="absolute inset-0 border-2 border-transparent group-hover:border-orange-500 rounded-xl transition-all duration-300" />
    </motion.div>
  );
};

export default GameCard;
