'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { useStore } from '@/store/useStore';
import { Category } from '@/types';
import { cn } from '@/utils';
import categoriesData from '@/data/categories.json';

const CategoryFilter: React.FC = () => {
  const { selectedCategory, setSelectedCategory, language } = useStore();
  
  const categories = [
    {
      id: 'all',
      name: 'All Games',
      nameVi: 'Tất Cả',
      icon: '🎯',
      color: 'from-gray-500 to-gray-700'
    },
    ...categoriesData
  ] as (Category & { id: string })[];

  return (
    <div className="px-4 py-6">
      <div className="max-w-7xl mx-auto">
        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-2xl md:text-3xl font-bold text-center mb-8 bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent"
        >
          {language.code === 'vi' ? 'Danh Mục Trò Chơi' : 'Game Categories'}
        </motion.h2>

        <div className="flex flex-wrap justify-center gap-4">
          {categories.map((category, index) => (
            <motion.button
              key={category.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              whileHover={{ scale: 1.05, y: -2 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setSelectedCategory(category.id)}
              className={cn(
                'relative overflow-hidden rounded-xl p-6 min-w-[140px] transition-all duration-300',
                selectedCategory === category.id
                  ? 'ring-4 ring-orange-500 ring-opacity-50 shadow-2xl'
                  : 'hover:shadow-xl'
              )}
            >
              {/* Background Gradient */}
              <div className={cn(
                'absolute inset-0 bg-gradient-to-br opacity-90',
                category.color
              )} />
              
              {/* Content */}
              <div className="relative z-10 text-center text-white">
                <div className="text-3xl mb-2">{category.icon}</div>
                <div className="font-semibold text-sm md:text-base">
                  {language.code === 'vi' ? category.nameVi : category.name}
                </div>
                {category.id !== 'all' && (
                  <div className="text-xs opacity-80 mt-1">
                    {language.code === 'vi' ? category.description : category.descriptionEn}
                  </div>
                )}
              </div>

              {/* Active Indicator */}
              {selectedCategory === category.id && (
                <motion.div
                  layoutId="activeCategory"
                  className="absolute inset-0 bg-white bg-opacity-20 rounded-xl"
                  transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                />
              )}

              {/* Hover Effect */}
              <div className="absolute inset-0 bg-white bg-opacity-0 hover:bg-opacity-10 transition-all duration-300 rounded-xl" />
            </motion.button>
          ))}
        </div>

        {/* Category Description */}
        <motion.div
          key={selectedCategory}
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mt-6"
        >
          {selectedCategory !== 'all' && (
            <p className="text-gray-600 max-w-2xl mx-auto">
              {(() => {
                const category = categoriesData.find(cat => cat.id === selectedCategory);
                return category 
                  ? (language.code === 'vi' ? category.description : category.descriptionEn)
                  : '';
              })()}
            </p>
          )}
        </motion.div>
      </div>
    </div>
  );
};

export default CategoryFilter;
