'use client';

import React, { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useStore } from '@/store/useStore';
import { useGames } from '@/hooks/useGames';
import GameCard from '@/components/Game/GameCard';
import Button from '@/components/UI/Button';

const GameList: React.FC = () => {
  const { selectedCategory, searchQuery, language } = useStore();
  const { games, loading, getGamesByCategory, getHotGames, getNewGames, searchGames } = useGames();
  const [displayCount, setDisplayCount] = useState(12);
  const [activeTab, setActiveTab] = useState<'all' | 'hot' | 'new'>('all');

  const filteredGames = useMemo(() => {
    let result = games;

    // Apply search filter
    if (searchQuery) {
      result = searchGames(searchQuery);
    } else {
      // Apply category filter
      result = getGamesByCategory(selectedCategory);
    }

    // Apply tab filter
    switch (activeTab) {
      case 'hot':
        result = result.filter(game => game.isHot);
        break;
      case 'new':
        result = result.filter(game => game.isNew);
        break;
      default:
        break;
    }

    return result;
  }, [games, selectedCategory, searchQuery, activeTab, getGamesByCategory, searchGames]);

  const displayedGames = filteredGames.slice(0, displayCount);
  const hasMore = filteredGames.length > displayCount;

  const loadMore = () => {
    setDisplayCount(prev => prev + 12);
  };

  const tabs = [
    { id: 'all' as const, label: language.code === 'vi' ? 'Tất Cả' : 'All Games' },
    { id: 'hot' as const, label: language.code === 'vi' ? 'Hot' : 'Hot Games' },
    { id: 'new' as const, label: language.code === 'vi' ? 'Mới' : 'New Games' }
  ];

  if (loading) {
    return (
      <div className="px-4 py-8">
        <div className="max-w-7xl mx-auto">
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 gap-4">
            {Array.from({ length: 12 }).map((_, index) => (
              <div
                key={index}
                className="bg-gray-200 animate-pulse rounded-xl aspect-[4/3]"
              />
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="px-4 py-8">
      <div className="max-w-7xl mx-auto">
        {/* Section Header */}
        <div className="flex flex-col md:flex-row justify-between items-center mb-8">
          <motion.h2
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            className="text-2xl md:text-3xl font-bold mb-4 md:mb-0"
          >
            {searchQuery ? (
              <>
                {language.code === 'vi' ? 'Kết quả tìm kiếm cho' : 'Search results for'}{' '}
                <span className="text-orange-500">"{searchQuery}"</span>
              </>
            ) : (
              <>
                {selectedCategory === 'all' 
                  ? (language.code === 'vi' ? 'Tất Cả Trò Chơi' : 'All Games')
                  : (language.code === 'vi' ? 'Trò Chơi' : 'Games')
                }
                {filteredGames.length > 0 && (
                  <span className="text-gray-500 text-lg ml-2">
                    ({filteredGames.length})
                  </span>
                )}
              </>
            )}
          </motion.h2>

          {/* Tab Filters */}
          {!searchQuery && (
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              className="flex bg-gray-100 rounded-lg p-1"
            >
              {tabs.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => {
                    setActiveTab(tab.id);
                    setDisplayCount(12);
                  }}
                  className={`px-4 py-2 rounded-md text-sm font-medium transition-all duration-200 ${
                    activeTab === tab.id
                      ? 'bg-white text-orange-600 shadow-sm'
                      : 'text-gray-600 hover:text-gray-900'
                  }`}
                >
                  {tab.label}
                </button>
              ))}
            </motion.div>
          )}
        </div>

        {/* Games Grid */}
        {filteredGames.length === 0 ? (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center py-16"
          >
            <div className="text-6xl mb-4">🎮</div>
            <h3 className="text-xl font-semibold text-gray-900 mb-2">
              {language.code === 'vi' ? 'Không tìm thấy trò chơi' : 'No games found'}
            </h3>
            <p className="text-gray-600">
              {searchQuery 
                ? (language.code === 'vi' 
                    ? 'Thử tìm kiếm với từ khóa khác' 
                    : 'Try searching with different keywords')
                : (language.code === 'vi' 
                    ? 'Chọn danh mục khác để xem trò chơi' 
                    : 'Select a different category to view games')
              }
            </p>
          </motion.div>
        ) : (
          <>
            <motion.div
              layout
              className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 gap-4 md:gap-6"
            >
              <AnimatePresence>
                {displayedGames.map((game, index) => (
                  <motion.div
                    key={game.id}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                    transition={{ delay: index * 0.05 }}
                    layout
                  >
                    <GameCard game={game} />
                  </motion.div>
                ))}
              </AnimatePresence>
            </motion.div>

            {/* Load More Button */}
            {hasMore && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="text-center mt-12"
              >
                <Button
                  variant="outline"
                  size="lg"
                  onClick={loadMore}
                  className="px-8"
                >
                  {language.code === 'vi' ? 'Xem Thêm' : 'Load More'} ({filteredGames.length - displayCount})
                </Button>
              </motion.div>
            )}
          </>
        )}

        {/* Quick Stats */}
        {filteredGames.length > 0 && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
            className="mt-12 bg-gradient-to-r from-blue-50 to-purple-50 rounded-2xl p-6"
          >
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
              <div>
                <div className="text-2xl font-bold text-blue-600">{filteredGames.length}</div>
                <div className="text-sm text-gray-600">
                  {language.code === 'vi' ? 'Tổng Trò Chơi' : 'Total Games'}
                </div>
              </div>
              <div>
                <div className="text-2xl font-bold text-red-600">
                  {filteredGames.filter(g => g.isHot).length}
                </div>
                <div className="text-sm text-gray-600">
                  {language.code === 'vi' ? 'Hot Games' : 'Hot Games'}
                </div>
              </div>
              <div>
                <div className="text-2xl font-bold text-green-600">
                  {filteredGames.filter(g => g.isNew).length}
                </div>
                <div className="text-sm text-gray-600">
                  {language.code === 'vi' ? 'Trò Chơi Mới' : 'New Games'}
                </div>
              </div>
              <div>
                <div className="text-2xl font-bold text-purple-600">
                  {new Set(filteredGames.map(g => g.provider)).size}
                </div>
                <div className="text-sm text-gray-600">
                  {language.code === 'vi' ? 'Nhà Cung Cấp' : 'Providers'}
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default GameList;
