'use client';

import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useStore } from '@/store/useStore';
import Button from '@/components/UI/Button';

const Banner: React.FC = () => {
  const { language, isAuthenticated, setRegisterModalOpen } = useStore();
  const [currentSlide, setCurrentSlide] = useState(0);

  const bannerSlides = [
    {
      id: 1,
      title: language.code === 'vi' ? 'Chào Mừng Đến FA CHAI Gaming' : 'Welcome to FA CHAI Gaming',
      subtitle: language.code === 'vi' 
        ? 'Trải nghiệm cá cược đỉnh cao với hàng nghìn trò chơi hấp dẫn' 
        : 'Experience premium betting with thousands of exciting games',
      background: 'from-blue-600 via-purple-600 to-blue-800',
      cta: language.code === 'vi' ? 'Đăng Ký Ngay' : 'Register Now'
    },
    {
      id: 2,
      title: language.code === 'vi' ? 'Khuyến Mãi Khủng 100%' : 'Massive 100% Bonus',
      subtitle: language.code === 'vi' 
        ? 'Nhận ngay 5,000,000 VNĐ tiền thưởng cho lần nạp đầu tiên' 
        : 'Get up to 5,000,000 VND bonus on your first deposit',
      background: 'from-orange-500 via-red-500 to-pink-600',
      cta: language.code === 'vi' ? 'Nhận Thưởng' : 'Claim Bonus'
    },
    {
      id: 3,
      title: language.code === 'vi' ? 'Casino Trực Tuyến #1' : '#1 Live Casino',
      subtitle: language.code === 'vi' 
        ? 'Dealer xinh đẹp, tỷ lệ thắng cao, rút tiền siêu tốc' 
        : 'Beautiful dealers, high winning rates, instant withdrawals',
      background: 'from-green-500 via-teal-500 to-blue-600',
      cta: language.code === 'vi' ? 'Chơi Ngay' : 'Play Now'
    }
  ];

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % bannerSlides.length);
    }, 5000);

    return () => clearInterval(timer);
  }, [bannerSlides.length]);

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % bannerSlides.length);
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + bannerSlides.length) % bannerSlides.length);
  };

  const handleCTAClick = () => {
    if (!isAuthenticated) {
      setRegisterModalOpen(true);
    }
  };

  return (
    <div className="relative h-[500px] md:h-[600px] overflow-hidden rounded-2xl mx-4 my-6">
      <AnimatePresence mode="wait">
        <motion.div
          key={currentSlide}
          initial={{ opacity: 0, scale: 1.1 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0, scale: 0.9 }}
          transition={{ duration: 0.7 }}
          className={`absolute inset-0 bg-gradient-to-br ${bannerSlides[currentSlide].background}`}
        >
          {/* Background Pattern */}
          <div className="absolute inset-0 opacity-10">
            <div className="absolute inset-0" style={{
              backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.1'%3E%3Ccircle cx='30' cy='30' r='4'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`,
            }} />
          </div>

          {/* Content */}
          <div className="relative h-full flex items-center justify-center text-center px-4">
            <div className="max-w-4xl mx-auto">
              <motion.h1
                initial={{ y: 50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.2, duration: 0.6 }}
                className="text-4xl md:text-6xl font-bold text-white mb-6"
              >
                {bannerSlides[currentSlide].title}
              </motion.h1>
              
              <motion.p
                initial={{ y: 30, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.4, duration: 0.6 }}
                className="text-xl md:text-2xl text-white text-opacity-90 mb-8 max-w-2xl mx-auto"
              >
                {bannerSlides[currentSlide].subtitle}
              </motion.p>

              <motion.div
                initial={{ y: 30, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.6, duration: 0.6 }}
                className="flex flex-col sm:flex-row gap-4 justify-center items-center"
              >
                <Button
                  variant="primary"
                  size="lg"
                  onClick={handleCTAClick}
                  className="bg-white text-gray-900 hover:bg-gray-100 shadow-2xl px-8 py-4 text-lg font-semibold"
                >
                  {bannerSlides[currentSlide].cta}
                </Button>
                
                <Button
                  variant="outline"
                  size="lg"
                  className="border-white text-white hover:bg-white hover:text-gray-900 px-8 py-4 text-lg"
                >
                  {language.code === 'vi' ? 'Tìm Hiểu Thêm' : 'Learn More'}
                </Button>
              </motion.div>

              {/* Stats */}
              <motion.div
                initial={{ y: 30, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.8, duration: 0.6 }}
                className="mt-12 grid grid-cols-3 gap-8 max-w-2xl mx-auto"
              >
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-white">1000+</div>
                  <div className="text-white text-opacity-80 text-sm md:text-base">
                    {language.code === 'vi' ? 'Trò Chơi' : 'Games'}
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-white">50K+</div>
                  <div className="text-white text-opacity-80 text-sm md:text-base">
                    {language.code === 'vi' ? 'Người Chơi' : 'Players'}
                  </div>
                </div>
                <div className="text-center">
                  <div className="text-3xl md:text-4xl font-bold text-white">24/7</div>
                  <div className="text-white text-opacity-80 text-sm md:text-base">
                    {language.code === 'vi' ? 'Hỗ Trợ' : 'Support'}
                  </div>
                </div>
              </motion.div>
            </div>
          </div>
        </motion.div>
      </AnimatePresence>

      {/* Navigation Arrows */}
      <button
        onClick={prevSlide}
        className="absolute left-4 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-30 hover:bg-opacity-50 text-white p-3 rounded-full transition-all duration-200"
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
        </svg>
      </button>

      <button
        onClick={nextSlide}
        className="absolute right-4 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-30 hover:bg-opacity-50 text-white p-3 rounded-full transition-all duration-200"
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
        </svg>
      </button>

      {/* Dots Indicator */}
      <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 flex space-x-2">
        {bannerSlides.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentSlide(index)}
            className={`w-3 h-3 rounded-full transition-all duration-200 ${
              index === currentSlide 
                ? 'bg-white' 
                : 'bg-white bg-opacity-50 hover:bg-opacity-75'
            }`}
          />
        ))}
      </div>
    </div>
  );
};

export default Banner;
